}.van-divider {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  margin: 0.426667rem 0;
  color: #969799;
  font-size: 0.373333rem;
  line-height: 0.64rem;
  border-color: #ebedf0;
  border-style: solid;
  border-width: 0;
}

.van-divider::after,
.van-divider::before {
  display: block;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  box-sizing: border-box;
  height: 0.026667rem;
  border-color: inherit;
  border-style: inherit;
  border-width: 0.026667rem 0 0;
}

.van-divider::before {
  content: '';
}

.van-divider--hairline::after,
.van-divider--hairline::before {
  -webkit-transform: scaleY(.5);
