  right: 0;
  top: -1.333333rem;
  height: 1.333333rem;
  line-height: 1.333333rem;
  padding: 0 0.4rem;
  font-size: 0.373333rem;
}

.van-field__left-icon {
  line-height: inherit;
}

/**/

.dialog_tip_wrapper {
  padding: 0.533333rem 0;
}

.dialog_tip_wrapper .tx1 {
  padding: 0 0.933333rem;
  font-size: 0.4rem;
  color: #666666;
  line-height: 1.8;
  margin-bottom: 0.4rem;
}

.dialog_tip_wrapper .tx2 {
  font-size: 0.346667rem;
  color: #999999;
  text-align: center;
  line-height: 2;
}

.dialog_tip_wrapper .red {
  color: #ff0000;
}

.dialog_tip_wrapper .blue {
  color: #1787fb;
  text-decoration: none;
}

/**/

.dialog_wx_qr {
  padding: 0.213333rem 0 0.453333rem;
}

.dialog_wx_qr .tip {
  font-size: 0.346667rem;
  text-align: center;
  margin-bottom: 0.666667rem;
}

.dialog_wx_qr .orange {
  color: #ff6600;
}

.dialog_wx_qr .qr_img {
  width: 4.133333rem;
  height: 4.133333rem;
  margin: 0 auto 0.533333rem;
}

.dialog_wx_qr .qr_img img {
  width: 4.133333rem;
  height: 4.133333rem;
  border: 0;
}

.dialog_wx_qr .tip_text {
  font-size: 0.346667rem;
  line-height: 1.8;
  text-align: left;
  padding: 0 0.8rem;
  word-break: break-all;
}

.arrow_for::after {
  position: absolute;
  right: 0.453333rem;
  top: 0.56rem;
  width: 0.186667rem;
  height: 0.186667rem;
  border-top: 0.026667rem solid #999999;
  border-right: 0.026667rem solid #999999;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  content: "";
}

.absolute_for_x {
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}

.absolute_for_y {
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
}

/**/

.alw-layer {
  top: 0;
  z-index: 50000;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8) url(../../static/img/wzp_layer.a620907.png) no-repeat center;
  background-size: 8.533333rem auto;
  position: fixed;
}

.alw-wx-layer {
  top: 0;
  z-index: 50000;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8) url(../../static/img/wx_layer.d4b421d.png) no-repeat center top;
  background-size: contain;
  position: fixed;
}